import { Directive,ElementRef } from '@angular/core';

@Directive({
  selector: '[textonly]'
})
export class CustomDirectveDirective {
numberrex = /^[a-zA-Z ]*$/;
  constructor(private ef:ElementRef) { 
      this.ef.nativeElement.onkeypress=(evt)=>{
           let chr=String.fromCharCode(evt.charCode);
           if(!this.numberrex.test(chr))
           evt.preventDefault();
      }
  }

}



