import { Routes } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { StudentComponent } from './student/student.component';
import { TeacherComponent } from './teacher/teacher.component';
import { CustomDirectveDirective } from './custom-directve.directive';
import { StudentinfoComponent } from './studentinfo/studentinfo.component';
import { TeacherinfoComponent } from './teacherinfo/teacherinfo.component';
import { StudentmarksComponent } from './studentmarks/studentmarks.component';
import { TeacherScheduleComponent } from './teacher-schedule/teacher-schedule.component';
import { StudentAttendenceComponent } from './student-attendence/student-attendence.component';
import { NotfoundComponent } from './notfound/notfound.component';
export const routes: Routes = [
    { path: 'login', component: LoginComponent },
    {
        path: 'student', component: StudentComponent,
        children: [
            { path: '', component: StudentinfoComponent },
            { path: 'studentmarks', component: StudentmarksComponent },
            { path: 'studentaattendence', component: StudentAttendenceComponent }
        ]
    },    
    {
        path: 'teacher', component: TeacherComponent,
        children: [
            { path: '', component: TeacherinfoComponent },
            { path: 'teacherschedule', component: TeacherScheduleComponent }
        ]
    },
    {path:'notfound',component:NotfoundComponent},
    { path: '', redirectTo:'login',pathMatch:'full' },
    { path: '**', redirectTo:'notfound' }

]


