import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {RouterModule} from '@angular/router';
import {ReactiveFormsModule,FormsModule} from '@angular/forms';
import {HttpClientModule } from '@angular/common/http'

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { StudentComponent } from './student/student.component';
import { TeacherComponent } from './teacher/teacher.component';
import { CustomDirectveDirective } from './custom-directve.directive';
import { StudentinfoComponent } from './studentinfo/studentinfo.component';
import { TeacherinfoComponent } from './teacherinfo/teacherinfo.component';
import { StudentmarksComponent } from './studentmarks/studentmarks.component';
import { TeacherScheduleComponent } from './teacher-schedule/teacher-schedule.component';
import { StudentAttendenceComponent } from './student-attendence/student-attendence.component';
import { NotfoundComponent } from './notfound/notfound.component';
import {routes} from './app.routes'
import {RestApiService} from './rest-api.service';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    StudentComponent,
    TeacherComponent,
    CustomDirectveDirective,
    StudentinfoComponent,
    TeacherinfoComponent,
    StudentmarksComponent,
    TeacherScheduleComponent,
    StudentAttendenceComponent,
    NotfoundComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    ReactiveFormsModule,
    FormsModule,
    HttpClientModule
  ],
  providers: [RestApiService],
  bootstrap: [AppComponent]
})
export class AppModule { }



